import { Http, HttpModule } from '@angular/http';
import { AppStoreService } from './app-store/app-store.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Footer, Header , DataTableModule , SharedModule , DataGridModule, PanelModule, DialogModule } from 'primeng/primeng';
import { AppComponent } from './app.component';
import { AppStoreComponent } from './app-store/app-store.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {HttpClientModule} from '@angular/common/http';
import { RetirementPortletComponent } from './retirement-portlet/retirement-portlet.component';
import { AnnouncementsComponent } from './announcements/announcements.component';
import { ApplicantPinRetrievalComponent } from './applicant-pin-retrieval/applicant-pin-retrieval.component';
import { FormsModule  } from '@angular/forms';


@NgModule({
  declarations: [
    AppComponent,
    AppStoreComponent,
    RetirementPortletComponent,
    AnnouncementsComponent,
    ApplicantPinRetrievalComponent
  ],
  imports: [
    BrowserModule,
    SharedModule,
    PanelModule,
    HttpModule,
    DataGridModule,
    DataTableModule,
    DialogModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule
    
  ],
  providers: [AppStoreService],
  bootstrap: [AppComponent]
})
export class AppModule { }



