import { Http } from '@angular/http';
import { AppStoreInterface } from './app-store-interface';
import { Component, OnInit } from '@angular/core';
import { AppStoreService } from './app-store.service';


@Component({
  selector: 'app-store',
  templateUrl: './app-store.component.html',
  styleUrls: ['./app-store.component.css']
})
export class AppStoreComponent {

  appStores: any[];
  applications: Object = {};
//('/assets/data/app-stores.json')
//https://jsonplaceholder.typicode.com/posts
  constructor(http:Http) { 
    http.get('/assets/data/app-stores.json')
    .subscribe(response => {
      console.log(response.json().applications);
      this.appStores = response.json().applications;
    })
  }

  // ngOnInit() {
  //   this.appstoreService.getAppStores().then(appStores => console.log("**************"+appStores));
  //   console.log("**************:"+this.appStores);
  // }

}



